<template>
  <div>
    <!-- Header -->
    <app-header headerClass="header--white"></app-header>

    <!-- Products -->
    <div class="products">
      <div class="container">
        <!-- Product Filters -->
        <div class="products__filters" :class="{display: filtersDisplayed}">
          <div class="product__filters-container">
            <!-- Filter -->
            <div class="filter display" id="category">
              <div class="filter__heading">
                <h3 class="filter__title">Collection</h3>
                <img class="filter__arrow" src="/img/icons/chevron.svg" alt="chevron-icon">
              </div>
              <div class="filter__body">
                <!-- Men -->
                <div class="checkbox">
                  <input type="checkbox" name="m" id="m" @click="addCollectionFilter('men')">
                  <label for="m">Men</label>
                </div>
                <!-- Women -->
                <div class="checkbox">
                  <input type="checkbox" name="w" id="w" @click="addCollectionFilter('women')">
                  <label for="w">Women</label>
                </div>
              </div>
            </div>
            <!-- Filter -->
            <div class="filter display" id="color">
              <div class="filter__heading">
                <h3 class="filter__title">Color</h3>
                <img class="filter__arrow" src="/img/icons/chevron.svg" alt="chevron-icon">
              </div>
              <div class="filter__body">
                <div 
                  class="filter__color" 
                  :class="{active: filters.color.includes('black')}" 
                  id="black" 
                  @click="addColorFilter('black')"
                >
                  <div class="filter__color-box bg-black"></div>
                </div>
                <div 
                  class="filter__color" 
                  :class="{active: filters.color.includes('white')}" 
                  id="white" 
                  @click="addColorFilter('white')"
                >
                  <div class="filter__color-box bg-white"></div>
                </div>
                <div 
                  class="filter__color" 
                  :class="{active: filters.color.includes('red')}" 
                  id="red" 
                  @click="addColorFilter('red')"
                >
                  <div class="filter__color-box bg-red"></div>
                </div>
                <div 
                  class="filter__color" 
                  :class="{active: filters.color.includes('blue')}" 
                  id="blue" 
                  @click="addColorFilter('blue')"
                >
                  <div class="filter__color-box bg-blue"></div>
                </div>
                <div 
                  class="filter__color" 
                  :class="{active: filters.color.includes('green')}"
                  id="green" 
                  @click="addColorFilter('green')"
                >
                  <div class="filter__color-box bg-green"></div>
                </div>
                <div 
                  class="filter__color" 
                  :class="{active: filters.color.includes('gray')}" 
                  id="gray" 
                  @click="addColorFilter('gray')"
                >
                  <div class="filter__color-box bg-gray"></div>
                </div>
                <div 
                  class="filter__color" 
                  :class="{active: filters.color.includes('yellow')}" 
                  id="yellow" 
                  @click="addColorFilter('yellow')"
                >
                  <div class="filter__color-box bg-yellow"></div>
                </div>
              </div>
            </div>
            <!-- Filter -->
            <div class="filter display" id="price">
              <div class="filter__heading">
                <h3 class="filter__title">Price</h3>
                <img class="filter__arrow" src="/img/icons/chevron.svg" alt="chevron-icon">
              </div>
              <div class="filter__body">
                <!-- Price Slider -->
                <div class="filter__price-inputs">
                  <div class="price-input">
                    $
                    <input type="text" id="minPrice" ref="minPrice" v-model="filters.price.minPrice">
                  </div>
                  <div class="price-input">
                    $
                    <input type="text" id="maxPrice" ref="maxPrice" v-model="filters.price.maxPrice">
                  </div>
                </div>
                <div ref="priceSlider"></div>
              </div>
            </div>
          </div>
        </div>
        <!-- Products Main -->
        <div class="products__main" v-if="filteredProducts">
          <!-- Products Sort -->
          <div class="products__top-bar">
            <h1 class="products__heading">T-Shirts <span class="number">({{ filteredProducts.length }})</span></h1>
            <!-- Filter Toggler -->
            <div class="product__filter-toggler">
              <img src="/img/icons/filters.svg" alt="filter-toggle-icon" @click="filtersDisplayed = !filtersDisplayed">
            </div>
          </div>
          <!-- Product List -->
          <div class="products__list">
            <app-product v-for="product in filteredProducts" :key="product.id" :product="product"></app-product>
          </div>
        </div>
      </div>
    </div>
    <!-- Subscription -->
    <app-subscription></app-subscription>
  </div>
</template>

<script>
  import Product from '../components/Product.vue';
  import Subscription from '../components/Subscription.vue';
  import Header from '../components/Header.vue';
  import noUiSlider from 'nouislider';
  import 'nouislider/distribute/nouislider.css';
  import axios from 'axios';

  export default {
    data() {
      return {
        products: '',
        filteredProducts: '',
        filters: {
          collection: [],
          color: [],
          price: {
            minPrice: 0,
            maxPrice: 50
          }
        },
        filtersDisplayed: false
      };
    },
    watch: {
      filtersDisplayed() {
        document.querySelector('body').classList.toggle('no-scroll');
      },
      filters: {
        handler() {
          if (this.products) {
            // Price filter
            this.filteredProducts = this.products.filter((product) => {
              if (product.price >= this.filters.price.minPrice && product.price <= this.filters.price.maxPrice) {
                return product;
              }
            })
            // Color Filter
            .filter((product) => {
              if (this.filters.color.length > 0) {
                if (this.filters.color.includes(product.color)) {
                  return product;
                }
              } else {
                return product;
              }
            })
            // Collection Filter
            .filter((product) => {
              if (this.filters.collection.length > 0) {
                if (this.filters.collection.includes(product.gender)) {
                  return product;
                }
              } else {
                return product;
              }
            });
          }
        },
        deep: true
      }
    },
    components: {
      'app-product': Product,
      'app-subscription': Subscription,
      'app-header': Header
    },
    methods: {
      loadProducts() {
        if (this.products.length === 0) {
          axios.get('products.json')
            .then((response) => {
              this.products = response.data.products;
              this.filteredProducts = response.data.products;
            });
        }
      },

      initializePriceSlider() {
        const priceSlider = this.$refs.priceSlider;
        const inputMin = this.$refs.minPrice;
        const inputMax = this.$refs.maxPrice;

        // Initialize Slider
        noUiSlider.create(priceSlider, {
          start: [this.filters.price.minPrice, this.filters.price.maxPrice],
          connect: true,
          range: {
            'min': 0,
            'max': 50
          }
        });

        priceSlider.noUiSlider.on('update', (values, handle) => {
          let value = values[handle];

          if (handle) {
            this.filters.price.maxPrice = value;
          } else {
            this.filters.price.minPrice = value;
          }
        });

        inputMin.addEventListener('change', () => {
          priceSlider.noUiSlider.set([this.filters.price.minPrice, null]);
        });

        inputMax.addEventListener('change', () => {
          priceSlider.noUiSlider.set([null, this.filters.price.maxPrice]);
        });
      },

      addColorFilter(color) {
        if (this.filters.color.includes(color)) {
          let i = this.filters.color.indexOf(color);
          this.filters.color.splice(i, 1);
        } else {
          this.filters.color.push(color);
        }
      },

      addCollectionFilter(collection) {
        if (this.filters.collection.includes(collection)) {
          let i = this.filters.collection.indexOf(collection);
          this.filters.collection.splice(i, 1);
        } else {
          this.filters.collection.push(collection);
        }
      }
    },
    mounted() {
      this.loadProducts();
      this.initializePriceSlider();
    }
  }
</script>

<style lang="scss" scoped>
  .checkbox input[type="checkbox"]:checked+label::after {
    background-image: url("/img/icons/checkmark-black.svg");
  }
</style>